import React from 'react';

function BankInfo(props) {
    const bankInfo = props.bankInfo;
    return (
        <>
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='card'>
                        <div className="card-header">
                            <h4 className="card-title">Thông tin ngân hàng {bankInfo.name}</h4>
                        </div>
                        <div className='card-body'>
                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Ngân Hàng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.name}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Tình trạng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.status === 1 ? (
                                        <span className="badge badge-success">Hoạt động</span>
                                    ) : (
                                        <span className="badge badge-danger">Đang Tắt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Đang Sử Dụng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.user_status === 1 ? (
                                        <span className="badge badge-success">Bật</span>
                                    ) : (
                                        <span className="badge badge-danger">Tắt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Giá mở ngân hàng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.opening_fee} đ (Vĩnh Viễn)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Giá mở tài khoản:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.account_opening_fee} đ (từ tài khoản thứ 2)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Mở tối đa:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.max_accounts} tài khoản
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className='text-info'>Giá nạp tiền vào TK APP:</strong>
                                </div>
                                <div className="col text-info">
                                    {bankInfo.app_deposit_fee} đ + {bankInfo.app_deposit_fee_percentage}% tổng bill/lượt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className='text-info'>Giá chuyển tiền trong TK APP:</strong>
                                </div>
                                <div className="col text-info">
                                    {bankInfo.app_transfer_fee} đ + {bankInfo.app_transfer_fee_percentage}% tổng bill/lượt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Giá kiểm tra số tài khoản tự động:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.auto_check_account_fee} đ / lượt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className="text-warning">Lưu ý quan trọng:</strong>
                                </div>
                                <div className="col text-warning">
                                    {bankInfo.important_note}
                                </div>
                            </div>
                            <div className='p-3'>
                                <div
                                    className="toolbar toolbar-bottom"
                                    role="toolbar"
                                    style={{ textAlign: "right" }}
                                >
                                    <button className="btn btn-danger sw-btn-next btn-sm" type="button">
                                        Đóng Ngân Hàng
                                    </button>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='card'>
                        <div className="card-header">
                            <h4 className="card-title">Danh sách tài khoản</h4>
                        </div>
                        <div className='card-body'>
                            <div className="table-responsive">
                                <div id="example_wrapper" className="dataTables_wrapper">
                                    <div className='row'>
                                        <div className='col-6'>
                                            <div id="example_filter" className="dataTables_filter">
                                                <label>
                                                    Tìm Kiếm :
                                                    <input type="search" className="" placeholder="" aria-controls="example" />
                                                </label>
                                            </div>
                                        </div>
                                        <div className='col-6'>
                                            <div
                                                className="toolbar toolbar-bottom"
                                                role="toolbar"
                                                style={{ textAlign: "right" }}
                                            >
                                                <button className="btn btn-primary sw-btn-next btn-sm" type="button">
                                                    Thêm Tài Khoản
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                    <table
                                        id="example"
                                        className="display dataTable"
                                        style={{ minWidth: 845 }}
                                        role="grid"
                                        aria-describedby="example_info"
                                    >
                                        <thead>
                                            <tr role="row">
                                                <th
                                                    className="sorting_asc"
                                                    tabIndex={0}
                                                    aria-controls="example"
                                                    rowSpan={1}
                                                    colSpan={1}
                                                    aria-sort="ascending"
                                                    aria-label="Name: activate to sort column descending"
                                                    style={{ width: "263.5px" }}
                                                >
                                                    Tên
                                                </th>
                                                <th
                                                    className="sorting"
                                                    tabIndex={0}
                                                    aria-controls="example"
                                                    rowSpan={1}
                                                    colSpan={1}
                                                    aria-label="Office: activate to sort column ascending"
                                                    style={{ width: "203.531px" }}
                                                >
                                                    Địa chỉ
                                                </th>
                                                <th
                                                    className="sorting"
                                                    tabIndex={0}
                                                    aria-controls="example"
                                                    rowSpan={1}
                                                    colSpan={1}
                                                    aria-label="Age: activate to sort column ascending"
                                                    style={{ width: "85.6562px" }}
                                                >
                                                    Tuổi
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr className="odd" role="row">
                                                <td className="sorting_1">Airi Satou</td>
                                                <td>Tokyo</td>
                                                <td>33</td>
                                            </tr>
                                            <tr className="even" role="row">
                                                <td className="sorting_1">Angelica Ramos</td>
                                                <td>London</td>
                                                <td>47</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default BankInfo;