import React, { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import VerifyCode from '../../models/VerifyCode';
import AppNotification from '../../helpers/AppNotification';
import UserModel from '../../models/UserModel';
import PaymentModel from '../../models/PaymentModel';
import ModalOwnerBank from './ModalOwnerBank';
import Swal from "sweetalert2";

const validationSchema = Yup.object().shape({
    amount: Yup.number()
        .required('Vui lòng nhập số tiền')
        .min(100000, 'Số tiền phải lớn hơn hoặc bằng 100.000đ'),
});

function ModalDeposit(props) {

    const ownerBank = props.bank;
    const [amount, setAmount] = useState();
    const [ depositIdcheck, setDepositIdcheck ] = useState();

    const [modalShow, setModalShow] = React.useState(false);
    const handleShowModal = () => {
        setModalShow(true);
    }
    const handleCloseModal = () => {
        setModalShow(false);
    };

    const initialValues = {
        amount: '',
        ownerbank_id : props.ownerid,
    };

    const [ownerBankSD, setOwnerBankSD] = useState({});
    const [ownerBankSDchange, setOwnerBankSDchange] = useState();

    useEffect(() => {
        if (ownerBankSDchange) {
            setModalShow(true);
            setOwnerBankSDchange(false);
        }
    }, [ownerBankSD, ownerBankSDchange, amount]);

    const handleSubmit = (values) => {
        console.log(values);
        PaymentModel.deposit(values).then(function (res) {
            setDepositIdcheck(res.data.id);
            setAmount(values.amount);
            setOwnerBankSD(ownerBank);
            setOwnerBankSDchange(true);
            AppNotification.showSuccess("Gửi yêu cầu Nạp tiền thành công!");
            console.log(res.data);
        }).catch(function (err) {
            Swal.fire({
                icon: "error",
                title: "Nạp tiền thất bại!",
                showConfirmButton: false,
                timer: 2000,
            });
            console.log(err);
            AppNotification.showError('Nạp tiền thất bại!!');
        })
    };
    // useEffect(() => {
    //     PaymentModel.depositDetail(depositIdcheck).then(function (res) {
    //         AppNotification.showSuccess("lấy chi tiết nạp tiền thành công!");
    //         console.log(res.data);
    //     }).catch(function (err) {
    //         console.log(err);
    //         AppNotification.showError('Lấy chi tiết thất bại!!');
    //     })
    // },[depositIdcheck , ownerBankSDchange]);

    useEffect(() => {
        const interval = setInterval(() => {
            PaymentModel.depositDetail(depositIdcheck).then(function (res) {
                AppNotification.showSuccess("lấy chi tiết nạp tiền thành công!");
                if(res.data.status == 1){
                    Swal.fire({
                        icon: "success",
                        title: "Nạp tiền thành công!",
                        showConfirmButton: false,
                        timer: 3000,
                    });
                    clearInterval(interval); 
                }
                console.log(res.data);
            }).catch(function (err) {
                console.log(err);
                AppNotification.showError('Lấy chi tiết thất bại!!');
            })
        }, 5000); 
        return () => {
          clearInterval(interval); 
        };
      }, [depositIdcheck , ownerBankSDchange]); 
    return (
        <>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        {ownerBank && `Nạp tiền vào tài khoản ${ownerBank.name}`}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Formik
                        initialValues={initialValues}
                        validationSchema={validationSchema}
                        onSubmit={handleSubmit}
                    >
                        {({ errors, touched }) => (
                            <Form>
                                <div className="mb-3">
                                    <label htmlFor="amount" className="form-label">
                                        Số tiền (*)
                                    </label>
                                    <Field
                                        type="number"
                                        id="amount"
                                        name="amount"
                                        className={`form-control ${errors.amount && touched.amount ? 'is-invalid' : ''
                                            }`}
                                        autoFocus
                                    />
                                    {errors.amount && touched.amount && (
                                        <div className="invalid-feedback">{errors.amount}</div>
                                    )}
                                </div>

                                <div className="modal-footer">
                                    <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={props.onHide}>
                                        Close
                                    </button>
                                    <button type="submit" className="btn btn-success">
                                        Xác nhận
                                    </button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </Modal.Body>
            </Modal>
            <ModalOwnerBank
                show={modalShow}
                onHide={handleCloseModal}
                ownerbank={ownerBankSD}
                amount={amount}
            />
        </>


    );
}

export default ModalDeposit;