import AppCookie from '../helpers/AppCookie';
import axiosClient from './axiosClient';

class AuthModel {
  constructor() {
    this.api_url = "/api/auth/";
  }
  async login(credentials) {
    const res = await axiosClient.post(this.api_url + "login", credentials);
    return res;
  }

  async register(credentials) {
    const res = await axiosClient.post(this.api_url + "register", credentials);
    return res;
  }

  async fogotpassword(email) {
    const res = await axiosClient.post(this.api_url + "forgot_password", email );
    return res;
  }

  async logout() {
    try {
      await axiosClient.post(this.api_url + "logout");
      return true;
    } catch (err) {
      return false;
    }
  }

  checkAuth(){
    let token = AppCookie.getCookie('token')
    if(token){
        return true;
    }
    return false;
  }
}

export default new AuthModel();