import React, { useState } from 'react';
import AuthLayout from "../layouts/AuthLayout";
import AuthMoldel from '../models/AuthMoldel';
import AppCookie from '../helpers/AppCookie';
import AppNotification from '../helpers/AppNotification';
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import { useNavigate } from 'react-router-dom';

const regiterSchema = Yup.object().shape({
    phone: Yup.string()
        .min(10, "Tối thiểu 10 số")
        .max(255, "Tối đa 255 kí tự")
        .required("Trường bắt buộc"),
    password: Yup.string()
        .min(6, "Tối thiểu 6 kí tự")
        .max(255, "Tối đa 255 kí tự ")
        .required("Trường bắt buộc"),
    name: Yup.string()
        .max(255, "Tối đa 255 kí tự ")
        .required("Trường bắt buộc"),
    email: Yup.string()
        .required("Trường bắt buộc"),
    password_confirmation: Yup.string()
        .min(6, "Tối thiểu 6 kí tự")
        .max(255, "Tối đa 255 kí tự ")
        .required("Trường bắt buộc"),
    referral_code: Yup.string()
        .required("Trường bắt buộc"),
});
function RegisterPage(props) {
    const [form] = useState({
        phone: "",
        password: "",
        name: "",
        email: "",
        password_confirmation: "",
        referral_code: "",
    })
    let navigate = useNavigate();
    const handleSubmit = (values) => {
        AuthMoldel.register(values).then((res) => {
            // Notification
            AppNotification.showSuccess('Đăng ký thành công!');

            // Redirect to dashboard
            navigate('/login')
        }).catch((err) => {
            console.log(err);
            AppNotification.showError('Đăng ký thất bại!');
        })
    }
    return (
        <AuthLayout>
            <div className="col-xl-12">
                <div className="card">
                    <div className="card-body ">
                        <div className="row">
                            <div className="col-xl-6 col-md-6 sign text-center">
                                <div>
                                    <img src="/images/log.png" className="education-img" />
                                </div>
                            </div>
                            <div className="col-xl-6 col-md-6">
                                <div className="sign-in-your">
                                    <h1>Đăng Ký</h1>
                                    <Formik
                                        initialValues={form}
                                        validationSchema={regiterSchema}
                                        onSubmit={(values) => handleSubmit(values)}
                                    >
                                        {({ errors, touched }) => (
                                            <Form className="mt-5">
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="phone">Số điện thoại</label>
                                                    <Field type="text" name="phone" className="form-control" />
                                                    {errors.phone && touched.phone ? (
                                                        <div style={{ color: 'red' }}>{errors.phone}</div>
                                                    ) : null}
                                                </div>
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="password">Mật khẩu</label>
                                                    <Field type="password" name="password" className="form-control" />
                                                    {errors.password && touched.password ? (
                                                        <div style={{ color: 'red' }}>{errors.password}</div>
                                                    ) : null}
                                                </div>
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="name">Tài khoản</label>
                                                    <Field type="text" name="name" className="form-control" />
                                                    {errors.name && touched.name ? (
                                                        <div style={{ color: 'red' }}>{errors.name}</div>
                                                    ) : null}
                                                </div>
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="email">Email</label>
                                                    <Field type="email" name="email" className="form-control" />
                                                    {errors.email && touched.email ? (
                                                        <div style={{ color: 'red' }}>{errors.email}</div>
                                                    ) : null}
                                                </div>
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="password_confirmation">Mật khẩu cấp 2</label>
                                                    <Field type="text" name="password_confirmation" className="form-control" />
                                                    {errors.password_confirmation && touched.password_confirmation ? (
                                                        <div style={{ color: 'red' }}>{errors.password_confirmation}</div>
                                                    ) : null}
                                                </div>
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="referral_code">Mã giới thiệu</label>
                                                    <Field type="text" name="referral_code" className="form-control" />
                                                    {errors.referral_code && touched.referral_code ? (
                                                        <div style={{ color: 'red' }}>{errors.referral_code}</div>
                                                    ) : null}
                                                </div>
                                                <div className="text-center pt-3">
                                                    <button type="submit" className="btn btn-primary btn-block">
                                                        Đăng Ký
                                                    </button>
                                                </div>
                                            </Form>
                                        )}
                                    </Formik>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </AuthLayout>
    );
}

export default RegisterPage;