import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import MasterLayout from '../layouts/MasterLayout';
import ThemeBreadcrumb from '../components/themes/ThemeBreadcrumb';
import BankItem from '../components/bank/BankItem';
import AuthMoldel from '../models/AuthMoldel';
import AppNotification from '../helpers/AppNotification';
import TransactionModel from '../models/TransactionModel';

function TransactionHistoryPage(props) {
    const navigate = useNavigate();
    const [items, setItems] = useState([]);

    useEffect(() => {
        TransactionModel.history({})
            .then(res => {
                setItems(res.data);
                console.log(res.data);
            }).catch(err => {
                console.log(err);
                AppNotification.showError('Lấy dữ liệu thất bại!');
            })
    },[]);
    return (
        <MasterLayout>
            <ThemeBreadcrumb title="Lịch Sử Giao Dịch" />
            <div className='row'>
                <div class="col-lg-12">
                    <div class="card">
                        <div class="card-header">
                            <h4 class="card-title">Lịch sử giao dịch</h4>
                        </div>
                        <div class="card-body">
                            <div className='row'>
                                <div className='col-lg-12'>
                                    <div className='input-group mb-3'>
                                        <input type='text' className='form-control' placeholder='Nhập từ khóa...' />
                                        <div className='input-group-append'>
                                            <button className='btn light btn-light' type='button'>Tìm kiếm</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="table-responsive">
                                <table class="table header-border table-responsive-sm">
                                    <thead>
                                        <tr>
                                            <th>ID</th>
                                            <th>Thể Loại</th>
                                            <th>Số tiền</th>
                                            <th>Ghi chú</th>
                                            <th>Thời gian</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            items.map((item, key) => (
                                                <tr key={key}>
                                                    <td><a href="">{item.id}</a>
                                                    </td>
                                                    <td><span class="badge badge-danger">{item.type}</span>
                                                    </td>
                                                    <td><span class="badge badge-success">{item.amount}</span>
                                                    </td>
                                                    <td>{item.note}</td>
                                                    <td><span class="text-muted">{item.create_at_fm}</span>
                                                    </td>
                                                </tr>
                                            ))
                                        }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </MasterLayout>
    );
}

export default TransactionHistoryPage;
