import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import VerifyCode from '../../models/VerifyCode';
import AppNotification from '../../helpers/AppNotification';
import UserModel from '../../models/UserModel';
import PaymentModel from '../../models/PaymentModel';
import ModalNotication from './ModalNotication';



function ModalOwnerBank(props) {

    const ownerBank = props.ownerbank;
    const amount = props.amount;
    const [modalShow, setModalShow] = React.useState(false);
    const handleCloseModal = () => {
        setModalShow(false);
    };

    return (
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Nạp tiền vào tài khoản {ownerBank.name}:
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="mb-3">
                        <div className='row'>
                            <div className='col-5 d-flex justify-content-center'>
                                <img src="https://skhdt.binhdinh.gov.vn/uploads/news/2022_06/image001.png" width="200" height="200" alt="My Image" />
                            </div>
                            <div className='col-6'>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>Số tài khoản:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{ownerBank.account_number}</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>Chủ Tài khoản:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{ownerBank.account_name}</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>Số tiền:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{amount} VNĐ</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-success'>Nội dung:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-success'>{ownerBank.description}</p>
                                    </div>
                                </div>
                                <div className='row pb-2'>
                                    <div className='col-6'>
                                        <strong className='text-warning'>Lưu ý:</strong>
                                    </div>
                                    <div className='col-6'>
                                        <p className='text-warning'>{ownerBank.note}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={props.onHide}>
                            Đóng
                        </button>
                    </div>
                </Modal.Body>
            </Modal>

    );
}

export default ModalOwnerBank;