import React, { useState } from 'react';
import AuthLayout from "../layouts/AuthLayout";
import AuthMoldel from '../models/AuthMoldel';
import AppCookie from '../helpers/AppCookie';
import AppNotification from '../helpers/AppNotification';
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import { useNavigate } from 'react-router-dom';

const loginSchema = Yup.object().shape({
    phone: Yup.string()
        .min(10, "Tối thiểu 10 số")
        .max(255, "Tối đa 255 kí tự")
        .required("Trường bắt buộc"),
    password: Yup.string()
        .min(6, "Tối thiểu 6 kí tự")
        .max(255, "Tối đa 255 kí tự ")
        .required("Trường bắt buộc")
});
function LoginPage(props) {
    const [form] = useState({
        phone: "",
        password: ""
    })
    let navigate = useNavigate();
    const handleSubmit = (values) => {
        AuthMoldel.login(values).then((res) => {
            // Notification
            AppNotification.showSuccess('Dang nhap thanh cong');

            //Set cookie
            AppCookie.setCookie('token', res.authorization.token);
            AppCookie.setCookie('user', JSON.stringify(res.data));

            // Redirect to dashboard
            navigate('/')
        }).catch((err) => {
            console.log(err);
            AppNotification.showError('Dang nhap that bai');
        })
    }
    return (
        <AuthLayout>
            <div className="col-xl-12">
                <div className="card">
                    <div className="card-body ">
                        <div className="row">
                            <div className="col-xl-6 col-md-6 sign text-center">
                                <div>
                                    <img src="/images/log.png" className="education-img" />
                                </div>
                            </div>
                            <div className="col-xl-6 col-md-6">
                                <div className="sign-in-your">
                                    <h1>Đăng nhập</h1>
                                    <span className="text-white mt-3">
                                        Chào mừng trở lại! Đăng nhập với dữ liệu bạn đã nhập
                                        <br /> trong quá trình đăng ký
                                    </span>

                                    <Formik
                                        initialValues={form}
                                        validationSchema={loginSchema}
                                        onSubmit={(values) => handleSubmit(values)}
                                    >
                                        {({ errors, touched }) => (
                                            <Form className="mt-5">
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="phone">Số điện thoại</label>
                                                    <Field type="text" name="phone" className="form-control" />
                                                    {errors.phone && touched.phone ? (
                                                        <div style={{ color: 'red' }}>{errors.phone}</div>
                                                    ) : null}
                                                </div>
                                                <div className="mb-3">
                                                    <label className="mb-1" htmlFor="password">Mật khẩu</label>
                                                    <Field type="password" name="password" className="form-control" />
                                                    {errors.password && touched.password ? (
                                                        <div style={{ color: 'red' }}>{errors.password}</div>
                                                    ) : null}
                                                </div>
                                                <div className="row d-flex justify-content-between mt-4 mb-2">
                                                    <div className="mb-3">
                                                        <a href="/forgot-password">Quên mật khẩu?</a>
                                                    </div>
                                                </div>
                                                <div className="text-center">
                                                    <button type="submit" className="btn btn-primary btn-block">
                                                        Đăng nhập
                                                    </button>
                                                </div>
                                            </Form>
                                        )}
                                    </Formik>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </AuthLayout>
    );
}

export default LoginPage;