import React, { useEffect, useState } from 'react';
import MasterLayout from '../layouts/MasterLayout';
import ThemeBreadcrumb from '../components/themes/ThemeBreadcrumb';
import BankItem from '../components/bank/BankItem';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import AppNotification from '../helpers/AppNotification';
import OwnerBankModel from '../models/OwnerBankModel';
import OwnerBankItem from '../components/paymentdeposit/OwnerBankItem';
import PaymentModel from '../models/PaymentModel';
import ModalDeposit from '../components/paymentdeposit/ModalDeposit';
function PaymentDepositPage(props) {
    
    const [show, setShow] = useState(false);
    const handleClose = () => {
        setShow(false)
    };

    const [modalShowTransfer, setModalShowTransfer] = React.useState(false);
    const [infoOwner, setInfoOwner] = useState();
    const [ownerid, setOwnerid] = useState();
    const [infoOwnerChanged, setInfoOwnerChanged] = useState();
    useEffect(() => {
        if (infoOwnerChanged) {
            setModalShowTransfer(true);
            setInfoOwnerChanged(false);
        }
    }, [infoOwner, infoOwnerChanged ,ownerid]);
    
    const handleShowModalTransfer = (bank) => {
        setOwnerid(bank.id);
        setInfoOwner(bank);
        setInfoOwnerChanged(true);
    };
    const handleCloseModalTransfer = () => {
        setModalShowTransfer(false);
    };

    const [banks, setBanks] = useState([]);
    useEffect(() => {
        OwnerBankModel.getOwnerBank({})
            .then(res => {
                setBanks(res.data);
            }).catch(err => {
                console.log(err);
                AppNotification.showError('Lấy dữ liệu thất bại!');
            })
    }, []);

    const [deposit, setDeposit] = useState([]);
    useEffect(() => {
        PaymentModel.getDeposit({})
        .then(res => {
            setDeposit(res.data);
            console.log(res.data);
        }).catch(err => {
            console.log(err);
            AppNotification.showError('Lấy lịch sử nạp tiền thất bại!');
        })
    }, []);

    return (
        <MasterLayout>
            <ThemeBreadcrumb title="Nạp tiền" />
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='row'>
                        <div className='col-xl-6'>
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">Nạp Tiền Tự Động</h4>
                                </div>
                                <div className="card-body p-3">
                                    <div className='row'>
                                        <div class="alert alert-dark solid alert-dismissible fade show custom-alert" style={{ backgroundColor: '#f7edd7' }}>
                                            <svg viewBox="0 0 24 24" width="24" height="24" stroke="#9F6D22" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round" class="me-2"><path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path><line x1="12" y1="9" x2="12" y2="13"></line><line x1="12" y1="17" x2="12.01" y2="17"></line></svg>
                                            <span style={{ color: '#9F6D22' }}>Vui lòng chuyển khoản đến 1 trong các cổng thanh toán phía dưới bằng cách click vào ngân hàng đó để hiển thị thông tin chuyển khoản.</span>
                                            <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="btn-close"></button>
                                        </div>
                                        <div class="alert alert-dark solid alert-dismissible fade show custom-alert" style={{ backgroundColor: '#f7edd7' }}>
                                            <svg viewBox="0 0 24 24" width="24" height="24" stroke="#9F6D22" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round" class="me-2"><path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path><line x1="12" y1="9" x2="12" y2="13"></line><line x1="12" y1="17" x2="12.01" y2="17"></line></svg>
                                            <span style={{ color: '#9F6D22' }}>Khi chuyển khoản thành công, vui lòng chờ nhận tiền vào tài khoản trước khi nạp đơn tiếp theo.</span>
                                            <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="btn-close"></button>
                                        </div>
                                        {
                                            banks.map((bank, key) => (
                                                <div className='col-xl-6' key={key}>
                                                    <a onClick={() => handleShowModalTransfer(bank)} >
                                                        <OwnerBankItem bank={bank} />
                                                    </a>
                                                </div>
                                            ))
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='col-xl-6'>
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">Hướng dẫn sử dụng</h4>
                                </div>
                                <div className="card-body p-3">
                                    <div className='row'>
                                        <div className='col-xl-12'>
                                            <iframe width="100%" height="405" src="https://www.youtube.com/embed/MHn66JJH5zs?si=-6UpbxTsSBnMWeMW" frameborder="0" allowfullscreen></iframe>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-12">
                    <div class="card">
                        <div class="card-header">
                            <h4 class="card-title">Lịch sử nạp tiền</h4>
                        </div>
                        <div class="card-body">
                            <div className='row'>
                                <div className='col-lg-12'>
                                    <div className='input-group mb-3'>
                                        <input type='text' className='form-control' placeholder='Nhập từ khóa...' />
                                        <div className='input-group-append'>
                                            <button className='btn light btn-light' type='button'>Tìm kiếm</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="table-responsive">
                                <table class="table header-border table-responsive-sm">
                                    <thead>
                                        <tr>
                                            <th>ID</th>
                                            <th>Số tiền</th>
                                            <th>Thực nhận</th>
                                            <th>Tình trạng</th>
                                            <th>Ghi chú</th>
                                            <th>Thời gian</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            deposit.map((item,key) => (
                                                <tr>
                                                    <td>{item.id}</td>
                                                    <td>{item.amount} VNĐ</td>
                                                    <td>{item.received} VNĐ</td>
                                                    {item.status == 1 ? <td><span class="badge badge-success">Đã duyệt</span></td> : <td><span class="badge badge-danger">Chờ duyệt</span></td>}
                                                    <td>{item.note}</td>
                                                    <td><span class="text-muted">{item.created_at}</span>
                                                    </td>
                                                </tr>
                                            ))
                                        }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
                <ModalDeposit
                    show={modalShowTransfer}
                    onHide={handleCloseModalTransfer}
                    bank={infoOwner}
                    ownerid={ownerid}
                />

                <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Nạp tiền vào tài khoản</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>Số tiền muốn nạp:
                        <Form.Control
                            type="email"
                            placeholder="500.000"
                            autoFocus
                        />
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="btn btn-danger" onClick={handleClose}>
                            Close
                        </Button>
                        <Button variant="btn btn-success" onClick={handleClose}>
                            Xác nhận
                        </Button>
                    </Modal.Footer>
                </Modal>

            </div>
        </MasterLayout>
    );
}

export default PaymentDepositPage;
