import React from 'react';

function HomeCardInfo(props) {
    return (
        <div className="widget-stat card">
            <div className="card-body p-4">
                <div className="media ai-icon"  style={{ display: 'flex', flexWrap: 'wrap', flexDirection: 'column', alignItems: 'flex-start' }}>
                        <div className="media-body pb-3" style={{ display: 'flex', alignItems: 'center' }} >
                            <h6 className="mb-0">{props.title_one}</h6>
                            <span className="badge badge-primary">{props.value_one}</span>
                        </div>
                        <div className="media-body" style={{ display: 'flex', alignItems: 'center' }} >
                            <h6 className="mb-0">{props.title_two}</h6>
                            <span className="badge badge-primary">{props.value_two}</span>
                        </div>
                </div>
            </div>
        </div>
    );
}

export default HomeCardInfo;