import MasterLayout from '../layouts/MasterLayout';
import ThemeBreadcrumb from '../components/themes/ThemeBreadcrumb';
import BankItem from '../components/bank/BankItem';
import { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import { InputGroup } from 'react-bootstrap';
import React, { useRef } from 'react';
import ModalTransfer from '../components/paymentEarnmoney/ModalTransfer';
import ModalWithdraw from '../components/paymentEarnmoney/ModalWithdraw';
import PaymentModel from '../models/PaymentModel';
import AppNotification from '../helpers/AppNotification';
import * as Yup from "yup";
import VerifyCode from '../models/VerifyCode';
import { Field, Formik } from 'formik';



function PaymentEarnMoneyPage(props) {

    const [modalShow, setModalShow] = React.useState(false);
    const handleShowModal = (type) => {
        setModalShow(true)
    }
    const handleCloseModal = () => {
        setModalShow(false);
    };

    const [modalShowTransfer, setModalShowTransfer] = React.useState(false);
    const handleShowModalTransfer = (type) => {
        setModalShowTransfer(true)
    }
    const handleCloseModalTransfer = () => {
        setModalShowTransfer(false);
    };

    const [info, setInfo] = useState({});
    const url_ref = process.env.REACT_APP_URL + info.user_name;

    const [withdrawHistory, setWithdrawHistory] = useState([]);

    useEffect(() => {
        PaymentModel.getWithdrawHistory().then((res) => {
            setWithdrawHistory(res.data);
            console.log(res.data);
        }).catch((err) => {
            console.log(err);
            AppNotification.showError('Lấy tin tức thất bại!');
        })
    }, [])

    const inputRef = useRef(null);
    const copyToClipboard = () => {
        inputRef.current.select();
        document.execCommand('copy');
        inputRef.current.blur();
    };

    useEffect(() => {
        PaymentModel.getInfo().then((res) => {
            setInfo(res.data);
            console.log(res.data);
        }).catch((err) => {
            console.log(err);
            AppNotification.showError('Lấy thông tin thất bại!');
        })
    }, [])
    return (
        <MasterLayout>
            <ThemeBreadcrumb title="Kiếm tiền" />
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='row'>
                        <div className="col-xl-6 col-lg-6">
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">Giới Thiệu Kiếm tiền Triệu</h4>
                                </div>
                                <div className="card-body">
                                    <div className="basic-form">
                                        <p>Link giới thiệu của bạn:</p>
                                        <div className="input-group mb-3">
                                            <input ref={inputRef} type="text" className="form-control" value={url_ref} />
                                            <button className="btn btn-primary" type="button" onClick={copyToClipboard}> Sao chép </button>
                                        </div>
                                        <p><strong>Số tiền có thể rút:</strong> <span style={{ marginLeft: '30px' }}>{info.can_earn_money}đ [Trực tiếp]</span></p>
                                        <p><strong>Số tài khoản con:</strong> <span style={{ marginLeft: '33px' }}>{info.count_account_chilrent} tài khoản</span></p>
                                        <p><strong>Tổng số khách nạp:</strong> <span style={{ marginLeft: '21px' }}>{info.count_people} tài khoản</span></p>
                                        <p><strong>Số tiền kiếm được:</strong> <span style={{ marginLeft: '26px' }}>{info.total_earn_money} đ</span></p>
                                        <p><strong>Hoa hồng trực tiếp:</strong> <span style={{ marginLeft: '23px' }}>{info.total_profit}% / đơn nạp thành công</span></p>
                                        <p><strong>Số lần đã rút tiền:</strong> <span style={{ marginLeft: '33px' }}>{info.count_withdraw_money} lần rút</span></p>
                                        <p><strong>Tổng số tiền đã rút:</strong> <span style={{ marginLeft: '23px' }}>{info.total_withdraw_money}đ</span></p>

                                        <div style={{ marginTop: '10px' }}>
                                            <button onClick={handleShowModal} className="btn btn-success">Rút tiền</button>
                                            <ModalWithdraw
                                                show={modalShow}
                                                onHide={handleCloseModal}
                                            />
                                            <button onClick={handleShowModalTransfer} className="btn btn-outline-light" style={{ marginLeft: '10px' }}>Chuyển tiền</button>
                                            <ModalTransfer
                                                show={modalShowTransfer}
                                                onHide={handleCloseModalTransfer}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className='col-xl-6'>
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">Hướng dẫn sử dụng</h4>
                                </div>
                                <div className="card-body p-3">
                                    <div className='row'>
                                        <div className='col-xl-12'>
                                            <iframe width="100%" height="315" src="https://www.youtube.com/embed/MHn66JJH5zs?si=-6UpbxTsSBnMWeMW" frameborder="0" allowfullscreen></iframe>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-12">
                    <div class="card">
                        <div class="card-header">
                            <h4 class="card-title">Lịch sử rút tiền</h4>
                        </div>
                        <div class="card-body">
                            <div className='row'>
                                <div className='col-lg-12'>
                                    <div className='input-group mb-3'>
                                        <input type='text' className='form-control' placeholder='Nhập từ khóa...' />
                                        <div className='input-group-append'>
                                            <button className='btn light btn-light' type='button'>Tìm kiếm</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="table-responsive">
                                <table class="table header-border table-responsive-sm">
                                    <thead>
                                        <tr>
                                            <th>ID</th>
                                            <th>Số tiền</th>
                                            <th>Thực nhận</th>
                                            <th>Ghi chú</th>
                                            <th>Tình trạng</th>
                                            <th>Thời gian</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            withdrawHistory.map((item, key) => (
                                                <tr key={key}>
                                                    <td>{ item.id }</td>
                                                    <td>{ item.amount } đ</td>
                                                    <td>{ item.received } đ</td>
                                                    <td>{ item.note }</td>
                                                    <td>{item.status === 1 ? <span class="badge badge-success">Thành công</span> : <span class="badge badge-danger">Chờ duyệt</span>}
                                                    </td>
                                                    <td><span class="text-muted">{ item.create_at_fm }</span>
                                                    </td>
                                                </tr>
                                            ))
                                        }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </MasterLayout>
    );
}

export default PaymentEarnMoneyPage;